import React from "react";
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";

const columnData = [
  { id: 'fullname', numeric: true, disablePadding: true, label: '' },
  { id: 'sex_tran', numeric: true, disablePadding: false, label: '' },
  { id: 'age', numeric: true, disablePadding: false, label: '' },
  { id: 'department_name', numeric: true, disablePadding: false, label: '' },
  { id: 'job_name', numeric: true, disablePadding: false, label: '' },
  { id: 'work_number', numeric: true, disablePadding: false, label: '' },
  { id: 'wechat', numeric: true, disablePadding: false, label: '' },
  { id: 'QQ', numeric: true, disablePadding: false, label: 'QQ' },
  { id: 'email', numeric: true, disablePadding: false, label: '' },
  { id: 'phone', numeric: true, disablePadding: false, label: '' }
];
const inputType = [];
@inject("usersInfoStores")
@observer
class usersList extends React.Component {
  componentDidMount() {
    this.props.usersInfoStores.getData();
  }
  render() {
    return (
      <ListTableComponent
        title=""
        operattext=""
        inputType={inputType}
        headTitlesData={columnData}
        data={this.props.usersInfoStores.user}
      />
    );
  }
}
export default usersList;
