import React from "react";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";
import TabComponent from "../component/tabComponent.jsx";

const styles = theme => ({
    listTableComponent: {
        marginTop: 0,
        borderRadius: "0 0 4px 4px",
        boxShadow: "0px 4px 5px 0px rgba(0, 0, 0, 0.1), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 3px 1px -2px rgba(0, 0, 0, 0.12)"
    }
});

let columnData = [
    { id: 'client_name', numeric: true, disablePadding: false, label: '' },
    { id: 'sales_name', numeric: true, disablePadding: false, label: '' },
    { id: 'machine_number', numeric: true, disablePadding: false, label: '' },
    { id: 'resourcetype', numeric: true, disablePadding: false, label: '' },
    { id: 'remove_status', numeric: true, disablePadding: false, label: '' },
    { id: 'operat', numeric: true, disablePadding: false, label: '',  extend: true, extendData: [
        {id: "business_number", label: "", type: "text"},
        {id: "business_note", label: "", type: "text"},
        {id: "remove_reason", label: "", type: "text"},
        {id: "resource_detail", label: "", type: "subordinate", subordinate: [
            {id: "machine_num", label: "", type: "text"},
            {id: "cpu", label: "CPU", type: "text"},
            {id: "memory", label: "", type: "text"},
            {id: "harddisk", label: "", type: "text"},
            {id: "bandwidth", label: "", type: "text"},
            {id: "protect", label: "", type: "text"},
            {id: "loginname", label: "", type: "text"},
            {id: "loginpass", label: "", type: "text"},
            {id: "machine_type", label: "", type: "text"},
            {id: "machine_note", label: "", type: "text"},
            {id: "cabinet_id", label: "", type: "text"}
          ]}
    ]}
];

const inputType = [
];

@inject("disposalHistorysStores")
@observer
class DisposalHistoryList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            value: 1
        };
    }
    componentDidMount() {
        this.props.disposalHistorysStores.getData(this.state.type);
    }
    handleChange = (value) => {
        this.props.disposalHistorysStores.getData(value);
        switch(value) {
            case 1:
            columnData = [
                { id: 'client_name', numeric: true, disablePadding: false, label: '' },
                { id: 'sales_name', numeric: true, disablePadding: false, label: '' },
                { id: 'machine_number', numeric: true, disablePadding: false, label: '' },
                { id: 'resourcetype', numeric: true, disablePadding: false, label: '' },
                { id: 'remove_status', numeric: true, disablePadding: false, label: '' },
                { id: 'operat', numeric: true, disablePadding: false, label: '',  extend: true, extendData: [
                    {id: "business_number", label: "", type: "text"},
                    {id: "business_note", label: "", type: "text"},
                    {id: "remove_reason", label: "", type: "text"},
                    {id: "resource_detail", label: "", type: "subordinate", subordinate: [
                        {id: "machine_num", label: "", type: "text"},
                        {id: "cpu", label: "CPU", type: "text"},
                        {id: "memory", label: "", type: "text"},
                        {id: "harddisk", label: "", type: "text"},
                        {id: "bandwidth", label: "", type: "text"},
                        {id: "protect", label: "", type: "text"},
                        {id: "loginname", label: "", type: "text"},
                        {id: "loginpass", label: "", type: "text"},
                        {id: "machine_type", label: "", type: "text"},
                        {id: "machine_note", label: "", type: "text"},
                        {id: "cabinet_id", label: "", type: "text"}
                      ]}
                ]}
            ]
            break;
            case 2:
            columnData = [
                { id: 'customer_name', numeric: true, disablePadding: false, label: '' },
                { id: 'business_name', numeric: true, disablePadding: false, label: '' },
                { id: 'resource', numeric: true, disablePadding: false, label: '' },
                { id: 'resourcetype', numeric: true, disablePadding: false, label: '' },
                { id: 'remove_status', numeric: true, disablePadding: false, label: '' },
                { id: 'operat', numeric: true, disablePadding: false, label: '',  extend: true, extendData: [
                    {id: "business_sn", label: "", type: "text"},
                    {id: "order_sn", label: "", type: "text"},
                    {id: "remove_reason", label: "", type: "text"},
                    {id: "machine_sn", label: "", type: "text"},
                ]}
            ]
            break;
        }
        this.setState({ value });
    }
    render() {
        const {classes} = this.props;
        return (
            <TabComponent onChange={this.handleChange} type={this.state.value} types={[
                {
                    label: "",
                    value: 1
                },
                {
                    label: "",
                    value: 2
                }
            ]}>
                <ListTableComponent
                    className={classes.listTableComponent}
                    title=""
                    operattext=""
                    inputType={inputType}
                    headTitlesData={columnData}
                    data={this.props.disposalHistorysStores.disposalHistorys}
                />
            </TabComponent>
        );
    }
}
DisposalHistoryList.propTypes = {
    classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(DisposalHistoryList);
