import React from "react";
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";
const qs = require('qs');

const columnData = [
    { id: 'customer_name', numeric: true, disablePadding: false, label: '' },
    { id: 'money_before', numeric: true, disablePadding: false, label: '' },
    { id: 'money_after', numeric: true, disablePadding: false, label: '' },
    { id: 'recharge_amount', numeric: true, disablePadding: false, label: '' },
    { id: 'recharge_way', numeric: true, disablePadding: false, label: '' },
    { id: 'timestamp', numeric: true, disablePadding: false, label: '' },
    { id: 'operat', numeric: true, disablePadding: false, extend: true, extendData: [
        {id: "customer_name", label: "", type: "text"},
        {id: "money_before", label: "" ,type: "text"},
        {id: "money_after", label: "" ,type: "text"},
        {id: "recharge_amount", label: "" ,type: "text"},
        {id: "recharge_way", label: "" ,type: "text"},
        {id: "salesman_name", label: "" ,type: "text"},
        {id: "trade_no", label: "" ,type: "text"},
        {id: "timestamp", label: "" ,type: "text"},
        {id: "voucher", label: "" ,type: "text"}
    ], label: '' }
];
const inputType = [
];
@inject("rechargesStores")
@observer
class RechargeList extends React.Component {
    componentDidMount() {
        if(location.search.indexOf("?id") > -1) {
            this.props.rechargesStores.getData({
                customer_id: qs.parse(location.search.substr(1)).id
            });
        } else {
            this.props.rechargesStores.getData();
        }
    }
    render() {
        return (
            <ListTableComponent
            title=""
            operattext=""
            inputType={inputType}
            headTitlesData={columnData}
            data={this.props.rechargesStores.recharge}
          />
        );
      }
}
export default RechargeList;
