import React from "react";
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";
import { routerConfig } from "../config/common/config.js";

const columnData = [
    { id: 'name', numeric: true, disablePadding: false, label: '' },
    { id: 'description', numeric: true, disablePadding: false, label: '' },
    { id: 'site', numeric: true, disablePadding: false, label: '' },
    { id: 'protection_value', numeric: true, disablePadding: false, label: '' },
    { id: 'price', numeric: true, disablePadding: false, label: '' },
    { id: 'operat', numeric: true, disablePadding: false, label: '', extend: true, extendUrl: [
        {
            title: "",
            link: routerConfig.baseUrl+"/defenseBusines",
            param: ["id"]
        }
      ] }
];

const inputType = [
    {
        field: "name",
        label: "",
        type: "text"
    },
    {
        field: "description",
        label: "",
        type: "text"
    },
    {
        field: "site",
        label: "",
        type: "select",
        defaultData: [{
            value: 1,
            text: ""
        }],
        model: {
            selectCode: (data) => {
              switch(data) {
                case "":
                    return 1;
              }
            }
        }
    },
    {
        field: "protection_value",
        label: "",
        type: "text"
    },
    {
        field: "price",
        label: "",
        type: "text"
    }
];

@inject("defensePackagesStores")
@observer
class DefensePackageList extends React.Component {
    componentDidMount() {
        this.props.defensePackagesStores.getData({
            site: 1
        });
    }
    delData = (selectedData,callbrak) => {
        const {defensePackagesStores} = this.props;
        let delIng = selectedData.map(item => defensePackagesStores.delData(item));
        callbrak(delIng);
    }
    addData = (param,callbrak) => {
        this.props.defensePackagesStores.addData(param).then((state) => {
            callbrak(state);
        });
    }
    changeData = (param,callbrak) => {
        const {defensePackagesStores} = this.props;
        defensePackagesStores.changeData(param).then((state) => {
          callbrak(state);
        });
    }
    render() {
        return (
            <ListTableComponent
                inputType={inputType}
                title=""
                operattext=""
                headTitlesData={columnData}
                addData={this.addData.bind(this)}
                changeData={this.changeData.bind(this)}
                delData={this.delData.bind(this)}
                data={this.props.defensePackagesStores.defensePackages}
            />
        );
    }
}
export default DefensePackageList;
