import React from "react";
import ListTableComponent from "../component/listTableComponent.jsx";
import { inject,observer } from "mobx-react";
import UnderOperation from "../component/dialog/underOperation.jsx";


const columnData = [
    { id: 'client_name', numeric: true, disablePadding: false, label: '' },
    { id: 'sales_name', numeric: true, disablePadding: false, label: '' },
    { id: 'machine_number', numeric: true, disablePadding: false, label: '' },
    { id: 'resource_type', numeric: true, disablePadding: false, label: '' },
    { id: 'removestatus', numeric: true, disablePadding: false, label: '' },
    { id: 'operat', numeric: true, disablePadding: false, label: '',  extend: true, extendData: [
        {id: "business_number", label: "", type: "text"},
        {id: "business_note", label: "", type: "text"},
        {id: "remove_reason", label: "", type: "text"},
        {id: "resource_detail", label: "", type: "subordinate", subordinate: [
            {id: "machine_num", label: "", type: "text"},
            {id: "cpu", label: "CPU", type: "text"},
            {id: "memory", label: "", type: "text"},
            {id: "harddisk", label: "", type: "text"},
            {id: "bandwidth", label: "", type: "text"},
            {id: "protect", label: "", type: "text"},
            {id: "loginname", label: "", type: "text"},
            {id: "loginpass", label: "", type: "text"},
            {id: "machine_type", label: "", type: "text"},
            {id: "machine_note", label: "", type: "text"},
            {id: "cabinet_id", label: "", type: "text"}
          ]}
    ], extendElement: (data,update) => {
        if(data.remove_status==1) {
            return <UnderOperation {...data} update={update} obtained_type={1} />;
        } else {
            return null;
        }

    } }
];

const columnData2 = [
    { id: 'customer_name', numeric: true, disablePadding: false, label: '' },
    { id: 'business_name', numeric: true, disablePadding: false, label: '' },
    { id: 'resource', numeric: true, disablePadding: false, label: '' },
    { id: 'resourcetype', numeric: true, disablePadding: false, label: '' },
    { id: 'removestatus', numeric: true, disablePadding: false, label: '' },
    { id: 'operat', numeric: true, disablePadding: false, label: '',  extend: true, extendData: [
        {id: "business_sn", label: "", type: "text"},
        {id: "order_sn", label: "", type: "text"},
        {id: "remove_reason", label: "", type: "text"},
        {id: "machine_sn", label: "", type: "text"},

    ],extendElement: (data,update) => {
        return <UnderOperation {...data} update={update} obtained_type={2} />;
    } }
]

const inputType = [
];

@inject("dismissalReviewsStores")
@observer
class DismissalReviewList extends React.Component {
    componentDidMount() {
        this.props.dismissalReviewsStores.getData();
    }
    updata() {
        this.props.dismissalReviewsStores.getData();
    }
    render() {
        return [
            <ListTableComponent
                title=""
                operattext=""
                inputType={inputType}
                headTitlesData={columnData}
                data={this.props.dismissalReviewsStores.dismissalReviews.business}
                otherConfig={{rowsPerPage: 5}}
                updata={this.updata.bind(this)}
            />,
            <ListTableComponent
                title=""
                operattext=""
                inputType={inputType}
                headTitlesData={columnData2}
                data={this.props.dismissalReviewsStores.dismissalReviews.orders}
                otherConfig={{rowsPerPage: 5}}
                updata={this.updata.bind(this)}
            />
        ];
    }
}
export default DismissalReviewList;
